body, html, div {
  font-family: 'Shadows Into Light', cursive;
  font-size: 20px;
}
body {
  background-color: black;
}

@-webkit-keyframes fadeToBlack {
  from {-webkit-clip-path: circle(1000px at center);clip-path: circle(1000px at center);};
  to {-webkit-clip-path: circle(0px at center);clip-path: circle(0px at center);}
}

@keyframes fadeToBlack {
  from {-webkit-clip-path: circle(1000px at center);clip-path: circle(1000px at center);};
  to {-webkit-clip-path: circle(0px at center);clip-path: circle(0px at center);}
}

@-webkit-keyframes newDay {
  from {opacity: 0};
  to {opacity: 1}
}

@keyframes newDay {
  from {opacity: 0};
  to {opacity: 1}
}

.App {
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100vh;
  color: white;
}

.App.selectionPhase {
  -webkit-animation-name: fadeToBlack;
          animation-name: fadeToBlack;
  -webkit-animation-duration: 9s;
          animation-duration: 9s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-clip-path: circle(0px at center);
          clip-path: circle(0px at center);
}

.App.newDay {
  -webkit-clip-path: none;
          clip-path: none;
  opacity: 1;
  -webkit-animation-name: newDay;
          animation-name: newDay;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.events {
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  background: #222222;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.characters {
  background: #000000;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 10px;
}

.character {
  -ms-flex-preferred-size: 200px;
      flex-basis: 200px;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.characterImageContainer {
  margin: 100px 0 0 100px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
}

.characterImage, .characterShield, .characterPancarte, .characterName {
  position: absolute;
  width: 180px;
  height: 180px;
  border-radius: 10000px;
  margin: -100px 0 0 -100px;
}

.characterPancarte {
  height: 190px;
  border-radius: 0;
  background: url('pancarte.png');
  background-size: 100%;
}

.characterImageContainer.rot5 > div {
  -webkit-transform: rotate(4deg);
      -ms-transform: rotate(4deg);
          transform: rotate(4deg);
}

.characterImageContainer.rot10 > div {
  -webkit-transform: rotate(7deg);
      -ms-transform: rotate(7deg);
          transform: rotate(7deg);
}

.characterImageContainer.rot15 > div {
  -webkit-transform: rotate(10deg);
      -ms-transform: rotate(10deg);
          transform: rotate(10deg);
}

.characterImageContainer.rot-5 > div {
  -webkit-transform: rotate(-4deg);
      -ms-transform: rotate(-4deg);
          transform: rotate(-4deg);
}

.characterImageContainer.rot-10 > div {
  -webkit-transform: rotate(-7deg);
      -ms-transform: rotate(-7deg);
          transform: rotate(-7deg);
}

.characterImageContainer.rot-15 > div {
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg);
}

.characterImageContainer.flip > video {
  -webkit-transform: scale(-1, 1);
      -ms-transform: scale(-1, 1);
          transform: scale(-1, 1);
}

.characterImage {
  width: 140px;
  height: 140px;
  margin: calc(-100px + 20px) 0 0 calc(-100px + 20px);
}

.characterImageBig {
  border-radius: 1000px;
  margin: 130px 0 0 267px;
  width: 450px;
  position: absolute;
}

.characterShield {
  background: url('hublot.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.characterName {
  text-align: center;
  padding-top: 154px;
}

.selected {
  background: green;
}

.dead {
  background: red;
}

.sacrificeAnnouncement, .factAnnouncement {
  background: blue;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url('popup2.png');
  background-size: cover;
  background-repeat: no-repeat;
}

.shamanMessage {
  margin: 390px 0 0 820px;
  width: 588px;
  color: black;
  font-size: 40px;
}

.sacrificeAnnouncement.visible {
  /*display: block;*/
}

.factAnnouncement.visible {
  /*display: block;*/
}



